<template>
   <v-row justify="center">
    <v-dialog v-model="dialog" persistent  max-width="800px">
      <v-card>
        <v-card-title>
          <span class="headline"> <v-icon color="primary">mdi-calendar-multiple-check</v-icon> Aylık Bülten Ekle</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
                <v-col cols="12" sm="12">
     <v-select
     :error-messages="errmessages"
     v-model="mnl.atMonth"
          :items="months"
           item-text="label"
          item-value="val"
         :label="$t('generate.selectMonth')"
        ></v-select>
    </v-col>
    <v-col cols="12">
        <el-upload
  class="upload-demo"
  drag
  :action="'#'"
  :on-change="fileUpload"
  :multiple="false"
  :limit="1"
  :auto-upload="false">
  <i class="el-icon-upload"></i>
  <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
  <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 2MB
    <p style="color:red"> {{errMsg}}</p>
  </div>
  <div style="color:red"></div>
</el-upload>

    </v-col>
            </v-row>
          </v-container>
        </v-card-text>
         <v-card-actions>
          <v-spacer></v-spacer>
           <v-btn  class="ma-2" @click="closeModal()" color="info" dark>{{$t('generate.close')}} </v-btn>
           <v-btn :loading="loading" :disabled="loading" class="ma-2" color="primary" @click="sendFood()" dark>{{$t('generate.submit')}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <snack-bar :snackbar="snackbar" @finished='snackbar=$event'></snack-bar>
  </v-row>
</template>

<script>
export default {
props:{
      dialog:{
        default:false,
        type : Boolean,
        required:true
    },
},
computed:{
dateRangeText () {
        return this.date.join(' ~ ')
      },
},
methods:{
 async sendFood(){
   this.loading=true
   this.errmessages=[]
   if(this.mnl.file.length==0|| this.mnl.atMonth==''){
     if(this.mnl.atMonth==''){
     this.errmessages.push('Bültenin hangi ay kullanılacağını giriniz.')
   }
   if(this.mnl.file.length==0){
     this.errMsg = "Dosya Seçiniz"
   }
   this.loading=false
   }
   else{
        this.loading = true
let fd = new FormData()
    fd.append('atMonth',this.mnl.atMonth)
    fd.append('image',this.mnl.file)
    let result = await this.$api.postfd('monthlyNewsletter','file',fd)
    if(result.status=='success'){
      let pos = this.months.map(function(e) { return e.val; }).indexOf(this.mnl.atMonth);
      this.$api.tnotify('Yeni Aylık Bülten Eklendi!', this.months[pos].label +' ayı Aylık Bülteniniz Eklenmiştir!','teachers')
      this.$api.tnotify('Yeni Aylık Bülten Eklendi!', this.months[pos].label +' ayı Aylık Bülteniniz Eklenmiştir!','parents')
      let type = 'jpg'
      if(this.mnl.file.type.includes('pdf'))
        type='pdf'
      let path = result.path.replace('public/',this.$api.BASE_API_URL)
      this.$emit('setNewFoodList',{atMonth: this.mnl.atMonth, file:path ,type:type})
         this.loading = false
      this.closeModal()
    }
    else{
         this.loading = false
   }


    }
  },
    fileUpload(e){
      if(e.raw.type=='application/pdf' || e.raw.type=='image/jpeg' || e.raw.type=='image/png'){
          if(e.size>2048000){
       this.errMsg = "Dosya Boyutunuz 2MB üzerinde lütfen " +e.raw.type +' dosyanızı kaldırın. Dosya Boyutu : ' +e.raw.size+' bayt'
      }
      else{
        this.errMsg=""
       this.mnl.file = e.raw;
      }
      }
      else{
          this.errMsg="Dosya formatınız .pdf, .jpg, .jpeg veya .png olmalıdır."
      }
    },
    closeModal(){
        this.dialog=false
        this.$emit('dialog',false)
        this.mnl={
             atMonth:"",
            file:[],
            dataURL:""
        }
    }
},
data(){
    return{
      months:[
        { val : 1, label:"Ocak"},
        { val : 2, label:"Şubat"},
        { val : 3, label:"Mart"},
        { val : 4, label:"Nisan"},
        { val : 5, label:"Mayıs"},
        { val : 6, label:"Haziran"},
        { val : 7, label:"Temmuz"},
        { val : 8, label:"Ağustos"},
        { val : 9, label:"Eylül"},
        { val : 10, label:"Ekim"},
        { val : 11, label:"Kasım"},
        { val : 12, label:"Aralık"},
      ],
      errmessages:[],
      snackbar:false,
       loading:false,
        date:[],
        errMsg:"",
        mnl:{
            atMonth:"",
            file:[],
            dataURL:""
        }
    }
},

}
</script>

<style>
@media only screen and (max-width: 900px) {
  .v-dialog {
    margin : 0px !important
  }
}
.el-upload, .el-upload-dragger {
    width: 100% !important;
}
</style>